<script>
  import Autocomplete from "../components/Autocomplete.svelte";
  import { auth } from "../Auth";
  import { get, post } from "../utils";

  let promises = Promise.all([
    get("/games", $auth.token),
    get("/activities/statuses", $auth.token)
  ]);
  let gameId;
  let isReplay = false;
  let statusId;
  let createdAt = new Date().toISOString().slice(0, 10);

  function submit() {
    // Clearing the input field makes the value an empty string which breaks validation
    createdAt = createdAt || null;
    post("/activities", { gameId, isReplay, statusId, createdAt }, $auth.token);
  }
</script>

{#await promises}
  <p>...waiting</p>
{:then results}
  <form on:submit|preventDefault={submit}>
    <Autocomplete
      items={results[0]}
      labelFieldName="name"
      valueFieldName="id"
      placeholder="Game"
      bind:value={gameId} />

    <label>
      Are you replaying this game?
      <input type="checkbox" bind:checked={isReplay} />
    </label>

    <Autocomplete
      items={results[1]}
      labelFieldName="name"
      valueFieldName="id"
      placeholder="Status"
      bind:value={statusId} />

    <label>
      What day did this happen?
      <input type="date" bind:value={createdAt} />
    </label>

    <button type="submit">Create</button>
  </form>
{:catch error}
  <p style="color: red">{error.message}</p>
{/await}
